export type StatusType = 'loading' | 'success';


export type InitialStateType = {
	status: StatusType
	error: string | null
}

const initialState:InitialStateType = {
	status:'success',
	error:null
}


export const appReducer = (state=initialState, action:ActionAppReducerType):InitialStateType => {
	switch (action.type) {
		case 'LOAD-PROGRESS':
			return {...state,
			...action.payload}
		case 'ERROR-STATUS'	:
			return {...state,
				...action.payload
			}
		default:
			return state
	}
}




export type ActionAppReducerType = ReturnType<typeof isLoadAC> | ReturnType<typeof isErrorAC>

export const isLoadAC = (status:StatusType) => {
	return {
		type:'LOAD-PROGRESS',
		payload:{
			status
		}
	} as const
}

export const isErrorAC = (error:string | null) => {
	return {
		type: 'ERROR-STATUS',
		payload:{
			error
		}
	} as const
}
